import TransitionEventController from './../../../base/controller/TransitionEvent.controller';
import { getExpandedDimensions, getUnExpandedDimensions } from './../../../helper/global.helper';
import fasterdom from 'fasterdom';
import Device from 'device';
import $ from 'jquery';

function addExpandHandler () {
	const self = this;

	this.expandHandler.on( 'click', ( e ) => {
		if ( this.isOpen ) {
			this.close();
		} else {
			this.open();
		}
	} );
}

function removeExpandHandler () {
	this.expandHandler.off( 'click' );
}

function checkExpandable ( breakpoint ) {
	if ( typeof breakpoint === 'undefined' || self.$tableColCount <= 4 ) {
		return;
	}

	if ( this.expandHandler === false ) {
		this.expandHandler = $( '<div class="trigger trigger-expand"></div>' );
		this.expandHandlerParent.append( this.expandHandler );
	}

	if ( typeof this.$scope.attr( 'aria-expanded' ) === 'undefined' ) {
		this.$scope.attr( 'aria-expanded', false );
	}

	if ( [ 'xs', 'sm', 'md' ].includes( breakpoint.alias ) ) {

		addExpandHandler.call( this );
	} else {
		removeExpandHandler.call( this );
	}
}

function addHandler () {

	this.onOpen( ( $trigger ) => {
		let _h = 0;

		fasterdom.measureInstant( () => {
			_h = getExpandedDimensions( this.$scope, 'height' ).outerHeight;
		} );

		fasterdom.mutateInstant( () => {
			this.$scope.css( { height : this.$scope.outerHeight() } );
		} );

		fasterdom.mutateInstant( ( a ) => {
			this.$scope.css( { height : _h } );
		}, 1 );
	} );

	this.afterOpen( ( $trigger ) => {
		fasterdom.mutateInstant( () => {
			this.$scope.css( { height : '' } );
		} );
	} );

	this.beforeClose( ( $trigger ) => {
		let _h = 0,
				_origH = 0;

		fasterdom.measureInstant( () => {
			_h = getExpandedDimensions( this.$scope, 'height' ).outerHeight;
			_origH = getUnExpandedDimensions( this.$scope, 'height' ).outerHeight;
		} );

		fasterdom.mutateInstant( () => {
			this.$scope.css( { height : _h } );
		} );

		fasterdom.mutateInstant( () => {
			this.$scope.css( { height : _origH } );
		}, 1 );
	} );

	this.afterClose( ( $trigger ) => {
		fasterdom.mutateInstant( () => {
			this.$scope.css( { height : '' } );
		} );
	} );
}

class ResponsiveTableItemController extends TransitionEventController {

	constructor () {
		super();
	}

	init ( $root ) {
		this.$scope              = $root;
		this.expandHandler       = false;
		this.expandHandlerParent = this.$scope.find( 'td:first' );

		this.setTransition( 'height', false );
		this.setTransitionElement( this.$scope );

		addHandler.call( this );

		Device.breakpointEvent.subscribe( ( data ) => {
			checkExpandable.call( this, data );
		} );
	}

}

export default ResponsiveTableItemController;



