import BaseModule from './../Base.module';
import ResponsiveTablesController from './controller/ResponsiveTables.controller';

/**
 * Module for tables to creates a responsive table with fixed first column
 * @extends {BaseModule}
 */
class ResponsiveTablesModule extends BaseModule {

	constructor ( config ) {

		super( config );
	}

	init () {
		super.init();

		const controllerInstance = new ResponsiveTablesController();

		this.responsiveTable = controllerInstance.init( this.node );
	}
}

export default ResponsiveTablesModule;



