import AbstractController from './../../../base/controller/Abstract.controller';
import ResponsiveTableItemController from './ResponsiveTableItem.controller';
import Device from 'device';
import $ from 'jquery';
import fasterdom from 'fasterdom';

/**
 * Markup polyfill for coremedia only
 * @private
 * @returns { void }
 */
function modifyMarkup () {
	const self     = this,
	      modifier = [];

	self.$table = self.$scope;

	// define modifier classes
	if ( self.$table.is( '[class*="-striped"]' ) ) {
		modifier.push( 'table--striped' );
	}

	if ( self.$table.is( '[class*="-bordered"]' ) ) {
		modifier.push( 'table--bordered' );
	}

	if ( self.$table.is( '[class*="-fixed"]' ) ) {
		modifier.push( 'table--fixed' );
	}

	// add wrapper
	if ( self.$table.hasClass( 'table-2-col' ) ) {
		fasterdom.mutateInstant( () => {
			self.$table.add( self.$table.prev( 'p.subheadline' ) ).wrapAll( '<div data-tpl="table-2-col" class="' + modifier.join( ' ' ) + '"/>' );
		} );
	}

	if ( self.$table.hasClass( 'table-2to12-col' ) ) {
		fasterdom.mutateInstant( () => {
			self.$table.add( self.$table.prev( 'p.subheadline' ) ).wrapAll( '<div data-tpl="table-2to12-col" class="' + modifier.join( ' ' ) + '"/>' );
			self.$table.wrap( '<div class="table-wrap-outer"><div class="table-wrap-inner"></div></div>' );
		} );
	}
}

/**
 * Shows/Hides drop shadow for fixed table cells while scrolling content
 * @private
 * @param { object } $el - jQuery wrapped DOM element
 * @returns { void }
 */
function toggleShadow ( $el ) {
	const self = this;

	if ( $el.scrollLeft() > 0 ) {
		fasterdom.mutateInstant( () => {
			self.$tableClone.parent().addClass( 'shadow' );
		} );
	} else {
		fasterdom.measureInstant( () => {
			self.$tableClone.parent().removeClass( 'shadow' );
		} );
	}
}

/**
 * Add event listener on table scrolling event when table clone is added
 * @private
 * @returns { void }
 */
function addHandler () {
	const self = this;

	self.$tableWrapInner.on( 'scroll', ( e ) => {
		toggleShadow.call( self, $( e.target ) );
	} );
}

/**
 * Removes event listener off table when table clone is deleted
 * @private
 * @returns { void }
 */
function removeHandler () {
	const self = this;

	self.$tableWrapInner.off( 'scroll' );
}

/**
 * Deletes table clone and removes event listeners
 * @private
 * @returns { void }
 */
function unsplitTable () {
	const self = this;

	fasterdom.mutateInstant( () => {
		self.$scope.removeClass( 'table--cloned' );
		self.$tableClone.unwrap().remove();
	} );

	removeHandler.call( self );
}

/**
 * Generates table clone and appends it to DOM / add event listeners
 * @private
 * @returns { void }
 */
function splitTable () {
	const self = this;

	fasterdom.mutateInstant( () => {
		self.$scope.addClass( 'table--cloned' );
		self.$tableClone = self.$table.clone();
		self.$tableWrapOuter.append( self.$tableClone );
		self.$tableClone.wrap( '<div class="table-clone" />' );

		addHandler.call( self );
		toggleShadow.call( self, self.$tableWrapInner );
	} );
}

/**
 * Switches the table mode by breakpoints
 * @private
 * @param { object } breakpoint - the current device breakpoint
 * @returns { void }
 */
function updateTable ( breakpoint ) {
	const self     = this,
	      hasClone = self.$scope.find( '.table-clone' ).length > 0;

	if ( typeof breakpoint !== 'undefined' ) {

		if ( self.$tableColCount > 4 && ( breakpoint.alias === 'lg' || breakpoint.alias === 'xl' || breakpoint.alias === 'xxl' ) ) {

			if ( !hasClone ) {
				splitTable.call( self );
			}
		} else if ( hasClone ) {
			unsplitTable.call( self );
		}
	}
}

/**
 * Class representing an responsive table instance.
 * @extends { AbstractController }
 */
class ResponsiveTablesController extends AbstractController {

	/**
	 * @param {moduleConfig} config - the config parsed from the DOM
	 */
	constructor ( config ) {
		super( config );

		this.$scope = null;

		this.$tableWrapOuter = null;
		this.$tableWrapInner = null;
		this.$table          = null;
		this.$tableClone     = null;
		this.$tableColCount  = 0;
		this.expandHandler   = false;
	}

	init ( $root ) {
		const self = this;

		this.$scope = $root;
		this.debug  = true;

		                   
                            
            

		if ( this.$scope.is( 'table' ) ) {
			this.$scope = this.$scope.parents( '[data-tpl]' ).first();
		}

		this.$table          = this.$scope.find( 'table' );
		this.$tableColCount  = this.$scope.find( 'tbody >  tr:first > td' ).length;
		this.$tableWrapInner = this.$scope.find( '.table-wrap-inner' );
		this.$tableWrapOuter = this.$scope.find( '.table-wrap-outer' );

		this.expandables     = this.$scope.find( 'tbody > tr' );

		fasterdom.measureInstant( () => {
			if ( self.$scope.hasClass( 'table--fixed' ) ) {
				Device.breakpointEvent.subscribe( ( data ) => {
					updateTable.call( self, data );
				} );
			}

			if ( self.$scope.hasClass( 'table--mobile-expandable' ) && this.$tableColCount >= 4 ) {
				this.expandables.each( ( i, v ) => {
					const ResponsiveTableItemInstance = new ResponsiveTableItemController();

					ResponsiveTableItemInstance.init( $( v ) );
				} );
			}
		} );
	}
}

export default ResponsiveTablesController;



